.feature {
    width: 100%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    margin: 1rem;
}

.feature-title {
    flex: 1;
    max-width: 180px;
    margin-right: 2rem;
}

.feature-title>h1 {
    font-family: var(--font-family);
    font-weight: 800;
    font-size: 18px;
    line-height: 24px;
    letter-spacing: -0.04em;
    color: white;
}

.feature-title>div {
    width: 38px;
    height: 3px;
    background: var(--gradient-bar);
    box-shadow: 0 4px 4px rgba(0,0,0,0.25);
    margin-bottom: 0.35rem;
}

.feature-text {
    flex: 2;
    max-width: 390px;
    display: flex;    
}

.feature-text>p {
    font-family: var(--font-family);
    font-weight: 500;
    font-size: 14px;
    line-height: 24px;
    color: var(--color-text);
}

